import "./styles.css";

export default function App() {
  const renderTable = () => {
    let rowData = [];
    const row = 3;
    const column = 3;

    for (let i = 0; i < row.length; i++) {
      rowData.push("<tr>");
      for (let j = 0; j < column.length; j++) {
        rowData.push(<td key={j} />);
      }
      rowData.push("</tr>");
    }

    return rowData;
  };
  const matrix = renderTable();
  return (
    <div className="App">
      <table>
        <tbody>
          {matrix.map((value) => {
            return value;
          })}
        </tbody>
      </table>
    </div>
  );
}
